@import url('https://fonts.googleapis.com/css2?family=Jost:wght@300;400;500;600;700&display=swap');

html, body {
    font-family: 'Jost';
    margin: 0;
    padding: 0;
    background-color: #000;
    color: #fff;
}


*,
*::before,
*::after {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
    font-weight: 400;
}

p {
    margin: 0;
    padding: 0;
    font-weight: 400;
}

button {
    border: 0;
    font-family: 'Jost';
    cursor: pointer;
}

header, section, footer {
    opacity: 0;
    transition: opacity .5s ease;
}

.content {
    position: relative;
    overflow: hidden;
}

.abstract {
    position: absolute;
    filter: blur(70px);
}

.noise {
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    background: url("../public/static/noise.png") no-repeat center top / cover;
}

.container {
    margin: 0 auto;
    width: 100%;
    max-width: 1180px;
}

.nav {
    padding-top: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__logo {
    width: 380px;
    height: 66px;
    display: block;
}

.nav__menu {
    display: flex;
    gap: 36px;
}

.nav__link {
    font-weight: 600;
    font-size: 20px;
    line-height: 1.4em;
    text-align: center;
    color: #B2ABF2;
    text-decoration: none;
    transition: color .3s ease;

    &:hover {
        color: #fff;
    }
}

.header {
    position: relative;
    z-index: 2;
    height: 100vh;
}

.abstract_one {
    width: 30%;
    left: 0;
    top: 0;
}

.abstract_two {
    width: 25%;
    right: 0;
    top: 50vh;
}

.abstract_three {
    width: 55%;
    left: 0;
    top: 100vh;
}

.abstract_four {
    width: 25%;
    right: 0;
    top: 200vh;
}

.abstract_five {
    width: 40%;
    left: 0;
    top: 300vh;
}

.header__container {
    position: relative;
    height: 100%;
}

.header__content {
    max-width: 680px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    max-height: 350px;
    max-height: max-content;
}

.header__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 1.2em;
    margin-bottom: 35px;
}

.header__description {
    font-size: 20px;
    line-height: 1.4em;
    max-width: 650px;
    color: #B2ABF2;
    margin-bottom: 35px;
}

.header__button {
    width: 300px;
    height: 60px;
    border-radius: 30px;
    font-weight: 700;
    font-size: 20px;
    line-height: 95%; 
    background-color: #564592;
    color: #fff;
    transition: background-color .3s ease, color .3s ease;

    &:hover {
        color: #564592;
        background-color: #fff;
    }
}

.header__image {
    position: absolute;
    right: 0;
    top: 100px;
    bottom: 0;
    margin: auto;
    max-height: 65%;
}

@media screen and (min-height: 1130px) {
    .header {
        height: auto;
    }

    .header__content {
        max-height: initial;
        position: static;
        margin: 150px 0 140px 0;
    }

    .header__image {
        max-height: 70%;
        top: auto;
        bottom: 0;
    }
}

@media screen and (max-height: 745px) {
    .header {
        height: auto;
    }

    .header__content {
        max-height: initial;
        position: static;
        margin: 150px 0 140px 0;
    }

    .header__image {
        max-height: 70%;
        top: auto;
        bottom: 0;
    }
}

.advantages {
    z-index: 2;
    position: relative;
    text-align: center;
}

@media screen and (max-height: 745px) {
    .advantages {
        margin-top: 100px;
    }
}

.advantages__title,
.prices__title,
.dev__title,
.about-us__title,
.form__title {
    font-weight: 600;
    font-size: 40px;
    line-height: 1.2em;
    color: #fff;
    margin-bottom: 20px;
}

.advantages__description,
.prices__description,
.about-us__description,
.form__description {
    font-weight: 300;
    font-size: 20px;
    line-height: 1.4em;
    max-width: 1020px;
    margin: auto;
}

.advantages__items {
    display: flex;
    justify-content: center;
    gap: 42px;
    margin: 60px auto 0 auto;
}

.advantages-item {
    max-width: 270px;
    text-align: left;
    position: relative;
    padding-left: 40px;
}

.advantages-item__title {
    font-weight: 600;
    font-size: 20px;
    line-height: 1.4em;
    margin-bottom: 12px;
    position: relative;
    z-index: 1;
}

.advantages-item__description {
    font-weight: 300;
    font-size: 20px;
    line-height: 1.4em;
    color: #B2ABF2;
}

.advantages-item__icon {
    position: absolute;
    left: 0;
    top: -20px;
}

.advantages-item_last {
    max-width: 305px;
}

.advantages-item_last .advantages-item__title {
    max-width: 220px;
}

.prices {
    z-index: 2;
    position: relative;
    margin-top: 140px;
    text-align: center;
}

.prices__description {
    max-width: 930px;
}

.prices__cards {
    margin-top: 40px;
    display: flex;
    gap: 60px;
    justify-content: center;
}

.prices-card {
    width: 280px;
    min-height: 360px;
    background-color: rgba(81, 81, 81, 0.15);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 0px 8px rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(25px);
    border-radius: 30px;
    padding: 24px 69px 32px 69px;
    cursor: pointer;
}

.prices-card_premium {
    background-color: rgba(178, 171, 242, 0.4);
    border: 1px solid rgba(178, 171, 242, 0.5);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 0px 8px rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(25px);

    & .prices-card__title {
        color: #B2ABF2;
    }

    & .prices-card__advantages {
        color: #fff;
    }
}

.prices-card__title {
    font-weight: 700;
    font-size: 24px;
    margin-bottom: 14px;
}

.prices-card__advantages {
    font-weight: 300;
    font-size: 18px;
    line-height: 1.4em;
    color: #B2ABF2;
    min-height: 100px;

    & span {
        display: block;
    }
}

.prices-card__price {
    margin-top: 45px;
    color: #B2ABF2;
    font-weight: 700;
    font-size: 48px;
    line-height: 100%;
}

.prices-card__time {
    font-size: 16px;
    margin-top: 3px;
}

.prices-card__description {
    font-size: 12px;
    margin-top: 45px;
}

.dev {
    z-index: 2;
    position: relative;
    margin-top: 140px;
    text-align: center;
}

.dev__items {
    margin-top: 65px;
    display: flex;
    justify-content: center;
    text-align: left;
    gap: 80px;

    & span {
        width: 1px;
        height: 100px;
        background-color: #564592;
    }
}

.dev__item {
    font-weight: 500;
    font-size: 20px;
    line-height: 1.4em;
    color: #B2ABF2;
    max-width: 241px;
}

.about-us {
    z-index: 2;
    position: relative;
    text-align: center;
    margin-top: 140px;
}

.about-us__description {
    max-width: 705px;
}

.about-us__devs {
    display: flex;
    flex-wrap: wrap;
    max-width: 1015px;
    gap: 45px;
    justify-content: center;
    margin: 70px auto 0 auto;
}

.about-us-dev__icon {
    width: 110px;
    height: 110px;
    border-radius: 50%;
    position: relative;
    margin: 0 auto 10px auto;

    &::before {
        content: "";
        display: block;
        width: 110px;
        height: 110px;
        border-radius: 50%;
        border: 1px solid #564592;
        position: absolute;
        right: -5px;
        top: -5px;
    }

    & img {
        position: relative;
        z-index: 1;
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
}

.about-us-dev__description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 60px;
    background-color: #000;
    border: 1px solid #564592;
    border-radius: 30px;
    font-weight: 300;
    font-size: 18px;
    line-height: 1.4em;
}

.form {
    z-index: 2;
    margin-top: 140px;
    text-align: center;
    position: relative;
}

.form__inner {
    position: relative;
}

.form__form {
    max-width: 485px;
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.form__input {
    height: 60px;
    border: 1px solid #564592;
    border-radius: 35px;
    background-color: transparent;
    padding: 18px 18px 18px 32px;
    font-family: 'Jost';
    font-weight: 300;
    font-size: 18px;
    line-height: 1.4em;
    color: #564592;
    transition: background-color .3s ease, color .3s ease;

    &::placeholder {
        color: #564592;
    }

    &:focus {
        background-color: #564592;
        color: #fff;
        outline: 0;

        &::placeholder {
            color: #fff;
        }
    }
}

.form__textarea {
    resize: none;
    height: 110px;
}

.form__button {
    background-color: #564592;
    border-radius: 35px;
    height: 60px;
    font-weight: 600;
    font-size: 20px;
    line-height: 1.4em;
    color: #fff;
    transition: background-color .3s ease, color .3s ease;
}

.form__image {
    position: absolute;
    top: 0;
    right: 0;
    width: 604px;
    height: 647px;
}